@font-face {
  font-family: press_start_2pregular;
  src: url(/fonts/pressstart2p-regular-webfont.eot);
  src: url(/fonts/pressstart2p-regular-webfont.eot?#iefix)
      format("embedded-opentype"),
    url(/fonts/pressstart2p-regular-webfont.woff2)
      format("woff2"),
    url(/fonts/pressstart2p-regular-webfont.woff)
      format("woff"),
    url(/fonts/pressstart2p-regular-webfont.ttf)
      format("truetype");
  font-weight: 400;
  font-style: normal;
}
